import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/admin'
    },
    // 首页
    {
      path: '/admin',
      name: 'Admin',
      component: () => import('pages/admin/Index')
    },
    // 工作看板
    {
      path: '/kanban',
      name: 'Kanban',
      component: () => import('pages/kanban/Index')
    },
    {
      path: '/kanban/detail/:id',
      name: 'KanbanDetail',
      component: () => import('pages/kanban/Detail')
    },
    {
      path: '/kanban/add',
      name: 'KanbanAdd',
      component: () => import('pages/kanban/Add')
    },
    // 属地管理
    {
      path: '/apanage',
      name: 'Apanage',
      component: () => import('pages/apanage/Index')
    },
    {
      path: '/apanage/detail/:id',
      name: 'ApanageDetail',
      component: () => import('pages/apanage/Detail')
    },
    {
      path: '/apanage/add',
      name: 'ApanageAdd',
      component: () => import('pages/apanage/Add')
    },
    // 故障提报
    {
      path: '/faultreporting',
      name: 'FaultReporting',
      component: () => import('pages/faultreporting/Index')
    },
    {
      path: '/faultreporting/detail/:id',
      name: 'FaultReportingDetail',
      component: () => import('pages/faultreporting/Detail')
    },
    {
      path: '/faultreporting/add',
      name: 'FaultReportingAdd',
      component: () => import('pages/faultreporting/Add')
    },
    // 规则制度查看
    {
      path: '/rules',
      name: 'Rule',
      component: () => import('pages/rules/Index')
    },
    // 打卡记录
    {
      path: '/clockrecords',
      name: 'ClockRecords',
      component: () => import('pages/clockRecords/Index')
    },
    // 换班申请
    {
      path: '/changeapplication',
      name: 'ChangeApplication',
      component: () => import('pages/changeApplication/Index')
    },
    {
      path: '/changeapplication/add',
      name: 'AddApplication',
      component: () => import('pages/changeApplication/Add')
    },
    {
      path: '/changeapplication/detail/:id',
      name: 'CDetial',
      component: () => import('pages/changeApplication/Detail')
    },
    // 班表考勤
    {
      path: '/attendancecheck',
      name: 'AttendanceRecords',
      meta: {
        item: 'plan'
      },
      component: () => import('pages/attendanceRecords/Index')
    }
  ]
})
