<template>
  <div class="weui-cell">
    <div class="weui-cell__bd">
      <div class="weui-uploader">
        <div v-show="showHeader" class="weui-uploader__hd">
          <p class="weui-uploader__title"> {{ title }} </p>
          <div v-show="showHeader && showTip" class="weui-uploader__info">
            {{ images.length }} / {{ max }}
          </div>
        </div>

        <div class="weui-uploader__bd" :class="{small: size === 'small'}">
          <div v-show="!readonly && images.length > 0" class="weui-uploader__input-box remove" @click="remove">
          </div>

          <ul class="weui-uploader__files">
            <uploader-item v-for="image in images" :background-image="image.url" :key="image.url" @click.native="preview(image)" v-preview="image.url"></uploader-item>
          </ul>

          <div v-show="!readonly && images.length < max" class="weui-uploader__input-box" @click="add">
            <input name="upload" v-if="!handleClick" ref="input" class="weui-uploader__input" type="file" accept="image/jpg,image/jpeg,image/png,image/gif" capture="camera" @change="change">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import UploaderItem from './UploaderItem.vue'
export default {
  props: {
    images: {
      type: Array,
      default: () => []
    },
    max: {
      type: Number,
      default: 1
    },
    showHeader: {
      type: Boolean,
      default: true
    },
    showTip: {
      type: Boolean,
      default: true
    },
    readonly: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '最多上传9张'
    },
    // 是否接管+号的click事件，如果是，点击不弹出选择文件的框
    handleClick: {
      type: Boolean,
      default: false
    },
    // 选择文件后是否自动上传，如果不是，则emit change事件
    autoUpload: {
      type: Boolean,
      default: true
    },
    uploadUrl: {
      type: String,
      default: ''
    },
    size: {
      type: String,
      default: 'normal'
    },
    capture: {
      type: String,
      default: ''
    },
    token: {
      type: String,
      default: ''
    }
  },
  components: {
    UploaderItem
  },
  methods: {
    add () {
      if (this.handleClick) {
        this.$emit('add-image')
      }
    },
    // 移除第一张图
    remove () {
      if (this.handleClick) {
        this.$emit('remove-image')
      } else {
        this.images.shift()
      }
    },
    preview (image) {
      // 暂未实现，可以捕捉preview事件自己实现
      this.$emit('preview', image)
    },
    // 适用于action的情况
    change () {
      if (this.handleClick) {
        return
      }

      let formData = new window.FormData()
      formData.append('img', this.$refs.input.files[0])

      let data = {
        file: this.$refs.input.files[0],
        token: this.token
      }
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      if (this.autoUpload) {
        if (!this.uploadUrl) {
          console.error('uploadUrl不应为空')
        }

        if (this.$vux && this.$vux.loading) {
          this.$vux.loading.show('正在上传')
        }

        let form = new FormData()
        form.append('file', this.$refs.input.files[0])
        form.append('token', this.token)
        let settings = {
          'async': true,
          'crossDomain': true,
          'url': this.uploadUrl,
          'method': 'POST',
          'headers': {
            'cache-control': 'no-cache',
            'postman-token': '57881cba-f761-e2c3-d468-b37c6ff01825'
          },
          'processData': false,
          'contentType': false,
          'mimeType': 'multipart/form-data',
          'data': form
        }

        var that = this
        $.ajax(settings).done(function (response) {
          response = JSON.parse(response)
          if (that.$vux && that.$vux.loading) {
            that.$vux.loading.hide()
          }
          that.$refs.input.value = ''
          var img = {
            url: 'http://p07mux2yp.bkt.clouddn.com/' + response.hash
          }
          that.images.push(img)
        })
      } else {
        this.$emit('upload-image', that.images)
      }
    }
  },
  computed: {
    showCapture () {
      return this.capture || undefined
    }
  }
}
</script>
<style scoped lang="less">
@import '../../styles/common';
.weui-cell{
  padding: 0;
  font-size: 12 * @px1;
}
.remove:before {
  width: 0;
  height: 0;
}
.weui-uploader__bd.small {
  .weui-uploader__input-box {
    margin-right: 12 * @px1;
    margin-bottom: 0;
    width: 56 * @px1;
    height: 56 * @px1;
  }
  .weui-uploader__file {
    width: 56 * @px1;
    height: 56 * @px1;
    margin-right: 12 * @px1;
  }
}
</style>

