import Vue from 'vue'
import FastClick from 'fastclick'
import VueMoment from 'vue-moment'
import moment from 'moment-timezone'
import App from './App.vue'
import router from './router'
import store from './store'
import rpc from 'services/rpc'
import config from 'services/config'
import NoItem from 'components/NoItem'
import BottomBtn from 'components/BottomBtn'
import CommonBtn from 'components/CommonBtn'
import MyDialog from 'components/MyDialog'
import ListItem from 'components/ListItem'
import Upload from 'components/upload/Upload'
import vuePicturePreview from 'components/vue_picture_preview'
import 'services/rem'

// rpc配置
const rpcConfig = config[process.env.VUE_APP_DDAPP]
rpc.setHost(rpcConfig.host, rpcConfig.db, rpcConfig.account_code)

Vue.config.productionTip = false
Vue.component('no-item', NoItem) // 没有数据时候的组件
Vue.component('bottom-btn', BottomBtn) // 底部按钮组件
Vue.component('common-btn', CommonBtn) // 一般按钮组件
Vue.component('my-dialog', MyDialog) // 遮罩层组件
Vue.component('list-item', ListItem) // 列表组件
Vue.component('upload', Upload) // 图片上传组件
Vue.use(vuePicturePreview) // 图片预览组件
Vue.use(VueMoment, { moment }) // moment.js

FastClick.attach(document.body)

// eslint-disable-next-line
if (process.env.NODE_ENV === 'production') {
  rpc.dingtalkLogin(null, location.href.replace(location.hash, ''), ['biz.util.uploadImage'], rpcConfig.agent_id).then(res => {
    Vue.prototype.$rpc = rpc
    new Vue({
      router,
      store,
      render: h => h(App)
    }).$mount('#app')
  }).catch(e => {
    console.log(e)
  })
}
